// –ú–æ–¥—É–ª—å —Å –æ—Å–Ω–æ–≤–Ω—ã–º–∏ —Ñ—É–Ω–∫—Ü–∏—è–º–∏ (–∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è, –∫–Ω–æ–ø–∫–∏, —Å–ª—É—à–∞—Ç–µ–ª–∏)

// –ò–º–ø–æ—Ä—Ç–∏—Ä—É–µ–º —Å—Ç–æ—Ä–æ–Ω–Ω–∏–µ –º–æ–¥—É–ª–∏
import {updateCounts} from './loadTasks.js';
import {getDate, setDate} from './localStorage.js';
import {addWindowEventListeners} from './eventHandlers.js';
import {createTaskModal, modalReset, taskModalConfirm} from './modal.js';
import {addEventListenerForDragAndDrop} from './drag&drop.js';
import {moveCard} from './cardTransfer.js';
import {hideTodo, hideProgress, hideCompleted} from './hideCategories.js';
import {deleteAllCompleted} from './deleteAllCompleted.js';
import {updateDeleteButtonVisibility} from './additionalFunctions.js';

// –ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞–ª–∏—á–∏—è –¥–∞–Ω–Ω—ã—Ö –≤ localStorage –∏ —Å–æ–∑–¥–∞–Ω–∏–µ –ø—É—Å—Ç–æ–≥–æ –º–∞—Å—Å–∏–≤–∞, –µ—Å–ª–∏ –¥–∞–Ω–Ω—ã—Ö –Ω–µ—Ç
if (!localStorage.getItem('tasks')) {
  localStorage.setItem('tasks', JSON.stringify([])); // –ï—Å–ª–∏ –µ–≥–æ –Ω–µ—Ç, —Ç–æ —Å–æ–∑–¥–∞–µ–º –µ–≥–æ —Å –ø—É—Å—Ç—ã–º –º–∞—Å—Å–∏–≤–æ–º –≤ –∫–∞—á–µ—Å—Ç–≤–µ –∑–Ω–∞—á–µ–Ω–∏—è
}

createTaskModal(); // –°–æ–∑–¥–∞—ë–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –¥–ª—è –¥–æ–±–∞–≤–ª–µ–Ω–∏—è –∏–ª–∏ —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏
export const modal = document.getElementById("modal");
export const cancelModal = document.getElementById("task__cancel");
export const taskConfirm = document.getElementById("task__confirm");
// –ü–æ–ª—É—á–∞–µ–º –∑–∞–≥–æ–ª–æ–≤–∫–∏ –∫–∞—Ç–µ–≥–æ—Ä–∏–π
export const todoHeader = document.getElementById('todo__header');
export const progressHeader = document.getElementById('progress__header');
export const completedHeader = document.getElementById('completed__header');
// –ü–æ–ª—É—á–∞–µ–º –ø–æ–ª—è —Å –∏–º–µ–Ω–µ–º, –æ–ø–∏—Å–∞–Ω–∏–µ–º –∏ –∏—Å–ø–æ–ª–Ω–∏—Ç–µ–ª–µ–º –∫–∞—Ä—Ç–æ—á–∫–∏
export const modalTitle = document.getElementById('modal__title');
export const modalDescription = document.getElementById('modal__description');
export const modalUser = document.getElementById('modal__user');
// –ü–æ–ª—É—á–∞–µ–º –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä—ã –∫–∞—Ç–µ–≥–æ—Ä–∏–π
export const todoContainer = document.getElementById('todo__container');
export const progressContainer = document.getElementById('progress__container');
export const completedContainer = document.getElementById('completed__container');
// –ü–æ–ª—É—á–∞–µ–º –∫–Ω–æ–ø–∫—É —É–¥–∞–ª–µ–Ω–∏—è –≤—Å–µ—Ö –∑–∞–≤–µ—Ä—à—ë–Ω–Ω—ã—Ö –∑–∞–¥–∞—á
export const deleteAll = document.getElementById('completed__delete-all');

// –°–æ–∑–¥–∞—ë–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é –¥–ª—è —Ö—Ä–∞–Ω–µ–Ω–∏—è —Ä–µ–¥–∞–∫—Ç–∏—Ä—É–µ–º–æ–π –∫–∞—Ä—Ç–æ—á–∫–∏ (–∏–∑–Ω–∞—á–∞–ª—å–Ω–æ null)
let editingCard = null;

addWindowEventListeners(); // –í—ã–∑–æ–≤ —Ñ—É–Ω–∫—Ü–∏–∏ –¥–ª—è –¥–æ–±–∞–≤–ª–µ–Ω–∏—è –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–æ–≤ —Å–æ–±—ã—Ç–∏–π –∫ –æ–∫–Ω—É
addEventListenerForDragAndDrop(); // –í—ã–∑–æ–≤ —Ñ—É–Ω–∫—Ü–∏–∏ –¥–ª—è –¥–æ–±–∞–≤–ª–µ–Ω–∏—è –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–æ–≤ —Å–æ–±—ã—Ç–∏–π –Ω–∞ Drag&Drop

// –î–æ–±–∞–≤–ª—è–µ–º —Å–ª—É—à–∞—Ç–µ–ª—å —Å–æ–±—ã—Ç–∏–π –Ω–∞ –≤–µ—Å—å –¥–æ–∫—É–º–µ–Ω—Ç
document.body.addEventListener('click', function(e) {
  if (e.target.classList.contains('card__edit')) { // –ü—Ä–æ–≤–µ—Ä–∫–∞, –∫–ª–∏–∫–Ω—É–ª–∏ –ª–∏ –ø–æ –∫–Ω–æ–ø–∫–µ "Edit"
    modal.style.display = "block";  // –ü–æ–∫–∞–∑—ã–≤–∞–µ–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
    // –ü–æ–ª—É—á–∞–µ–º —Å—Å—ã–ª–∫—É –Ω–∞ —Ä–µ–¥–∞–∫—Ç–∏—Ä—É–µ–º—É—é –∫–∞—Ä—Ç–æ—á–∫—É
    editingCard = e.target.closest('.card'); // –ü–æ–ª—É—á–∞–µ–º —Å—Å—ã–ª–∫—É –Ω–∞ —Ä–µ–¥–∞–∫—Ç–∏—Ä—É–µ–º—É—é –∫–∞—Ä—Ç–æ—á–∫—É
    let card = e.target.closest('.card');
    // –ó–∞–ø–æ–ª–Ω—è–µ–º –ø–æ–ª—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ –∑–Ω–∞—á–µ–Ω–∏—è–º–∏ –∏–∑ –∫–∞—Ä—Ç–æ—á–∫–∏
    modalTitle.value = card.querySelector('.card__title').textContent; 
    modalDescription.value = card.querySelector('.card__description').textContent;
    // –ü–æ–ª—É—á–∞–µ–º –∏–º—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –∏–∑ –∫–∞—Ä—Ç–æ—á–∫–∏
    const userName = card.querySelector('.card__user').textContent;
    for (let i = 0; i < modalUser.options.length; i++) { // –ù–∞—Ö–æ–¥–∏–º —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â–µ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –≤ —Å–µ–ª–µ–∫—Ç–æ—Ä–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ –∏ –≤—ã–±–∏—Ä–∞–µ–º –µ–≥–æ
      if (modalUser.options[i].text === userName) {
        modalUser.selectedIndex = i;
        break; // –í—ã—Ö–æ–¥–∏–º –∏–∑ —Ü–∏–∫–ª–∞, –∫–æ–≥–¥–∞ –Ω–∞—à–ª–∏ –Ω—É–∂–Ω–æ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
      }
    }
  }
  if (e.target.classList.contains('todo__add')) { // –ü—Ä–æ–≤–µ—Ä–∫–∞, –∫–ª–∏–∫–Ω—É–ª–∏ –ª–∏ –ø–æ –∫–Ω–æ–ø–∫–µ "Add todo"
    // –£–¥–∞–ª—è–µ–º —Å—Ç–∞—Ç—É—Å —Ä–µ–¥–∞–∫—Ç–∏—Ä—É–µ–º–æ–π –∫–∞—Ä—Ç–æ—á–∫–∏
    editingCard = null;
    // –ü–æ–∫–∞–∑—ã–≤–∞–µ–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
    modal.style.display = "block";
  }
  if (e.target === taskConfirm) { // –ü—Ä–æ–≤–µ—Ä–∫–∞, –∫–ª–∏–∫–Ω—É–ª–∏ –ª–∏ –ø–æ –∫–Ω–æ–ø–∫–µ "Confirm" –≤ –º–æ–¥–∞–ª—å–Ω–æ–º –æ–∫–Ω–µ —Å–æ–∑–¥–∞–Ω–∏—è –∏–ª–∏ —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏
    // –ü–µ—Ä–µ–¥–∞—ë–º –ø—É—Å—Ç—É—é –∏–ª–∏ —Ä–µ–¥–∞–∫—Ç–∏—Ä—É–µ–º—É—é –∫–∞—Ä—Ç–æ—á–∫—É
    taskModalConfirm(editingCard);
  }
  if (e.target === deleteAll) { // –ü—Ä–æ–≤–µ—Ä–∫–∞, –∫–ª–∏–∫–Ω—É–ª–∏ –ª–∏ –ø–æ –∫–Ω–æ–ø–∫–µ "Delete all" –¥–ª—è —É–¥–∞–ª–µ–Ω–∏—è –≤—Å–µ—Ö –∑–∞–≤–µ—Ä—à—ë–Ω–Ω—ã—Ö –∫–∞—Ä—Ç–æ—á–µ–∫
    // –ó–∞–ø—É—Å–∫–∞–µ–º —Ñ—É–Ω–∫—Ü–∏—é —É–¥–∞–ª–µ–Ω–∏—è –≤—Å–µ—Ö –∑–∞–≤–µ—Ä—à—ë–Ω–Ω—ã—Ö –∫–∞—Ä—Ç–æ—á–µ–∫
    deleteAllCompleted();
  }
  if (e.target === cancelModal) { // –ü—Ä–æ–≤–µ—Ä–∫–∞, –∫–ª–∏–∫–Ω—É–ª–∏ –ª–∏ –ø–æ –∫–Ω–æ–ø–∫–µ "Cancel" –≤ –º–æ–¥–∞–ª—å–Ω–æ–º –æ–∫–Ω–µ —Å–æ–∑–¥–∞–Ω–∏—è –∏–ª–∏ —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏
    // –ó–∞–∫—Ä—ã–≤–∞–µ–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
    modal.style.display = "none";
    // –û–±–Ω—É–ª—è–µ–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
    modalReset();
  }
  if (e.target.closest('.todo__header') === todoHeader) { // –°—Ö–ª–æ–ø—ã–≤–∞–Ω–∏–µ –∫–æ–ª–æ–Ω–∫–∏ ToDo
    hideTodo();
  }
  if (e.target.closest('.progress__header') === progressHeader) { // –°—Ö–ª–æ–ø—ã–≤–∞–Ω–∏–µ –∫–æ–ª–æ–Ω–∫–∏ In Progress
    hideProgress();
  }
  if (e.target.closest('.completed__header') === completedHeader) { // –°—Ö–ª–æ–ø—ã–≤–∞–Ω–∏–µ –∫–æ–ª–æ–Ω–∫–∏ Completed
    hideCompleted();
  }
  if (e.target.classList.contains('card__delete')) { // –ü—Ä–æ–≤–µ—Ä–∫–∞, –∫–ª–∏–∫–Ω—É–ª–∏ –ª–∏ –ø–æ –∫–Ω–æ–ø–∫–µ "Delete" –¥–ª—è —É–¥–∞–ª–µ–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏
    // –ù–∞—Ö–æ–¥–∏–º –∫–∞—Ä—Ç–æ—á–∫—É, –∫–æ—Ç–æ—Ä—É—é –Ω—É–∂–Ω–æ —É–¥–∞–ª–∏—Ç—å
    const card = e.target.closest('.card');
    // –ü–æ–ª—É—á–∞–µ–º id –∫–∞—Ä—Ç–æ—á–∫–∏ –∏ –ø—Ä–µ–æ–±—Ä–∞–∑—É–µ–º –µ–≥–æ –≤ —á–∏—Å–ª–æ
    const id = Number(card.dataset.id);
    // –£–¥–∞–ª—è–µ–º –∫–∞—Ä—Ç–æ—á–∫—É –∏–∑ DOM
    card.remove();
    // –ü–æ–ª—É—á–∞–µ–º —Ç–µ–∫—É—â–∏–µ –∑–∞–¥–∞—á–∏ –∏–∑ Local Storage
    const tasks = getDate();
    // –£–¥–∞–ª—è–µ–º –∑–∞–¥–∞—á—É —Å —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â–∏–º id –∏–∑ –º–∞—Å—Å–∏–≤–∞ –∑–∞–¥–∞—á
    const updatedTasks = tasks.filter(task => task.id !== id);
    // –û–±–Ω–æ–≤–ª—è–µ–º Local Storage —Å –Ω–æ–≤—ã–º –º–∞—Å—Å–∏–≤–æ–º –∑–∞–¥–∞—á
    setDate(updatedTasks);
    // –û–±–Ω–æ–≤–ª—è–µ–º —Å—á—ë—Ç—á–∏–∫–∏
    updateCounts();
    // –û–±–Ω–æ–≤–ª—è–µ–º –≤–∏–¥–∏–º–æ—Å—Ç—å –∫–Ω–æ–ø–∫–∏ —É–¥–∞–ª–µ–Ω–∏—è –≤—Å–µ—Ö –∫–∞—Ä—Ç–æ—á–µ–∫
    updateDeleteButtonVisibility();
  }

  if (e.target.classList.contains('card__forward') || e.target.classList.contains('card__back')) {
    const card = e.target.closest('.card'); // –ù–∞—Ö–æ–¥–∏–º –∫–∞—Ä—Ç–æ—á–∫—É, –∫ –∫–æ—Ç–æ—Ä–æ–π –æ—Ç–Ω–æ—Å–∏—Ç—Å—è –∫–Ω–æ–ø–∫–∞ (–±–ª–∏–∂–∞–π—à–∏–π —Ä–æ–¥–∏—Ç–µ–ª—å—Å–∫–∏–π —ç–ª–µ–º–µ–Ω—Ç —Å –∫–ª–∞—Å—Å–æ–º 'card')
    const currentStatus = card.parentElement.id.replace('__container', ''); // –ü–æ–ª—É—á–∞–µ–º —Ç–µ–∫—É—â–∏–π —Å—Ç–∞—Ç—É—Å –∏–∑ ID —Ä–æ–¥–∏—Ç–µ–ª—å—Å–∫–æ–≥–æ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ (—É–¥–∞–ª—è–µ–º '__container' –∏–∑ ID)

    // –û–ø—Ä–µ–¥–µ–ª—è–µ–º –Ω–æ–≤—ã–π —Å—Ç–∞—Ç—É—Å
    let newStatus; // –û–±—ä—è–≤–ª—è–µ–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é –¥–ª—è —Ö—Ä–∞–Ω–µ–Ω–∏—è –Ω–æ–≤–æ–≥–æ —Å—Ç–∞—Ç—É—Å–∞
    if (e.target.classList.contains('card__forward')) {// –ï—Å–ª–∏ –Ω–∞–∂–∞—Ç–∞ –∫–Ω–æ–ø–∫–∞ "–≤–ø–µ—Ä–µ–¥"
      switch (currentStatus) { // –ò—Å–ø–æ–ª—å–∑—É–µ–º switch –¥–ª—è –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è –Ω–æ–≤–æ–≥–æ —Å—Ç–∞—Ç—É—Å–∞ –Ω–∞ –æ—Å–Ω–æ–≤–µ —Ç–µ–∫—É—â–µ–≥–æ
        case 'todo':
          newStatus = 'progress'; // –ï—Å–ª–∏ —Ç–µ–∫—É—â–∏–π —Å—Ç–∞—Ç—É—Å 'todo', –Ω–æ–≤—ã–π —Å—Ç–∞—Ç—É—Å –±—É–¥–µ—Ç 'progress' 
          break; // –í—ã—Ö–æ–¥–∏–º –∏–∑ switch
        case 'progress':
          newStatus = 'completed'; // –ï—Å–ª–∏ —Ç–µ–∫—É—â–∏–π —Å—Ç–∞—Ç—É—Å 'progress', –Ω–æ–≤—ã–π —Å—Ç–∞—Ç—É—Å –±—É–¥–µ—Ç 'completed'
          break; // –í—ã—Ö–æ–¥–∏–º –∏–∑ switch
        // –î–ª—è "completed" –Ω–µ –Ω—É–∂–Ω–æ –Ω–∏—á–µ–≥–æ –¥–µ–ª–∞—Ç—å, —Ç–∞–∫ –∫–∞–∫ —ç—Ç–æ –∫–æ–Ω–µ—á–Ω—ã–π —Å—Ç–∞—Ç—É—Å
      }
    } else if (e.target.classList.contains('card__back')) { // –ï—Å–ª–∏ –Ω–∞–∂–∞—Ç–∞ –∫–Ω–æ–ø–∫–∞ "–Ω–∞–∑–∞–¥"
      switch (currentStatus) { // –ò—Å–ø–æ–ª—å–∑—É–µ–º switch –¥–ª—è –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è –Ω–æ–≤–æ–≥–æ —Å—Ç–∞—Ç—É—Å–∞ –Ω–∞ –æ—Å–Ω–æ–≤–µ —Ç–µ–∫—É—â–µ–≥–æ
        case 'progress':
          newStatus = 'todo'; // –ï—Å–ª–∏ —Ç–µ–∫—É—â–∏–π —Å—Ç–∞—Ç—É—Å 'progress', –Ω–æ–≤—ã–π —Å—Ç–∞—Ç—É—Å –±—É–¥–µ—Ç 'todo'
          break; // –í—ã—Ö–æ–¥–∏–º –∏–∑ switch
        case 'completed':
          newStatus = 'progress'; // –ï—Å–ª–∏ —Ç–µ–∫—É—â–∏–π —Å—Ç–∞—Ç—É—Å 'completed', –Ω–æ–≤—ã–π —Å—Ç–∞—Ç—É—Å –±—É–¥–µ—Ç 'progress'
          break; // –í—ã—Ö–æ–¥–∏–º –∏–∑ switch
        // –î–ª—è "todo" –Ω–µ –Ω—É–∂–Ω–æ –Ω–∏—á–µ–≥–æ –¥–µ–ª–∞—Ç—å, —Ç–∞–∫ –∫–∞–∫ —ç—Ç–æ –Ω–∞—á–∞–ª—å–Ω—ã–π —Å—Ç–∞—Ç—É—Å
      }
    }

    // –ü–µ—Ä–µ–º–µ—â–∞–µ–º –∫–∞—Ä—Ç–æ—á–∫—É, –µ—Å–ª–∏ –Ω–æ–≤—ã–π —Å—Ç–∞—Ç—É—Å –æ–ø—Ä–µ–¥–µ–ª–µ–Ω
    if (newStatus) { // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –±—ã–ª –ª–∏ –æ–ø—Ä–µ–¥–µ–ª–µ–Ω –Ω–æ–≤—ã–π —Å—Ç–∞—Ç—É—Å (—Ç.–µ. –Ω–µ –æ—Å—Ç–∞–ª—Å—è undefined)
      moveCard(card, newStatus); // –í—ã–∑—ã–≤–∞–µ–º —Ñ—É–Ω–∫—Ü–∏—é moveCard, –ø–µ—Ä–µ–¥–∞–≤–∞—è –∫–∞—Ä—Ç–æ—á–∫—É –∏ –Ω–æ–≤—ã–π —Å—Ç–∞—Ç—É—Å
    }
  }
});

// –û–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏—è –Ω–∞–∂–∞—Ç–∏—è –Ω–∞ –∫–Ω–æ–ø–∫—É 'Escape'
document.addEventListener('keydown', (e) => {
  if (e.key === 'Escape') {
    if (modal.style.display === "block") {
      // –ó–∞–∫—Ä—ã–≤–∞–µ–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
      modal.style.display = "none";
      // –û–±–Ω—É–ª—è–µ–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
      modalReset();
    }
  }
});

// –û–±—ä—è–≤–ª—è–µ–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é mouseDown –∏ —É—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –µ–µ –∑–Ω–∞—á–µ–Ω–∏–µ –≤ false. –≠—Ç–æ –±—É–¥–µ—Ç –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å—Å—è –¥–ª—è –æ—Ç—Å–ª–µ–∂–∏–≤–∞–Ω–∏—è —Å–æ—Å—Ç–æ—è–Ω–∏—è –∫–Ω–æ–ø–∫–∏ –º—ã—à–∏ (–Ω—É–∂–Ω–æ, —á—Ç–æ–±—ã –ø—Ä–∏ –≤—ã–¥–µ–ª–µ–Ω–∏–∏ —Ç–µ–∫—Å—Ç–∞ –∏ –ø–µ—Ä–µ–º–µ—â–µ–Ω–∏–∏ –∫—É—Ä—Å–æ—Ä–∞ –∑–∞ –ø—Ä–µ–¥–µ–ª—ã –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞, –æ–∫–Ω–æ –Ω–µ —Å–∫—Ä—ã–≤–∞–ª–æ—Å—å)
let mouseDown = false;

document.body.addEventListener('mousedown', function(e) { // –î–æ–±–∞–≤–ª—è–µ–º –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏–π –¥–ª—è —Å–æ–±—ã—Ç–∏—è 'mousedown'
  if (e.target === modal) { // –ü—Ä–æ–≤–µ—Ä–∫–∞, –∫–ª–∏–∫–Ω—É–ª–∏ –ª–∏ –ø–æ –º–æ–¥–∞–ª—å–Ω–æ–º—É –æ–∫–Ω—É
    mouseDown = true; // –ö–Ω–æ–ø–∫–∞ –º—ã—à–∏ –±—ã–ª–∞ –Ω–∞–∂–∞—Ç–∞ –≤–Ω—É—Ç—Ä–∏ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
  }
});

document.body.addEventListener('mouseup', function(e) { // –î–æ–±–∞–≤–ª—è–µ–º –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏–π –¥–ª—è —Å–æ–±—ã—Ç–∏—è 'mouseup'
  if (e.target === modal && mouseDown) {  // –ü—Ä–æ–≤–µ—Ä–∫–∞, –∫–ª–∏–∫–Ω—É–ª–∏ –ª–∏ –ø–æ –º–æ–¥–∞–ª—å–Ω–æ–º—É –æ–∫–Ω—É –∏ –±—ã–ª–∞ –ª–∏ –∫–Ω–æ–ø–∫–∞ –º—ã—à–∏ –Ω–∞–∂–∞—Ç–∞ –≤–Ω—É—Ç—Ä–∏ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
    modal.style.display = "none"; // –°–∫—Ä—ã–≤–∞–µ–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
    modalReset(); // –°–±—Ä–∞—Å—ã–≤–∞–µ–º —Å–æ—Å—Ç–æ—è–Ω–∏–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
  }
  mouseDown = false; // –í–æ–∑–≤—Ä–∞—â–∞–µ–º mouseDown –æ–±—Ä–∞—Ç–Ω–æ –≤ false
});
