// –ú–æ–¥—É–ª—å —Å —Ñ—É–Ω–∫—Ü–∏–µ–π –¥–ª—è –ø–µ—Ä–µ—Ç–∞—Å–∫–∏–≤–∞–Ω–∏—è –∫–∞—Ä—Ç–æ—á–µ–∫ –º–µ–∂–¥—É —Ä–∞–∑–¥–µ–ª–∞–º–∏

// –ò–º–ø–æ—Ä—Ç–∏—Ä—É–µ–º —Å—Ç–æ—Ä–æ–Ω–Ω–∏–µ –º–æ–¥—É–ª–∏
import {todoContainer, progressContainer, completedContainer} from './main.js'
import {updateCounts, createCard} from './loadTasks.js';
import {getDate, setDate} from './localStorage.js';
import {updateDeleteButtonVisibility} from './additionalFunctions.js';
import {createModal} from './modal.js';

export function moveCard(card, newStatus) {
    // –ü–æ–ª—É—á–∞–µ–º —Å—Å—ã–ª–∫–∏ –Ω–∞ –ø–æ–ª—è –∫–∞—Ä—Ç–æ—á–∫–∏ –∏ id
    const currentTitleElement = card.getElementsByClassName('card__title')[0]; // –ü–æ–ª—É—á–∞–µ–º —ç–ª–µ–º–µ–Ω—Ç –∑–∞–≥–æ–ª–æ–≤–∫–∞ –∫–∞—Ä—Ç–æ—á–∫–∏
    const currentTitle = currentTitleElement.innerHTML; // –ò–∑–≤–ª–µ–∫–∞–µ–º —Ç–µ–∫—Å—Ç –∑–∞–≥–æ–ª–æ–≤–∫–∞ –∏–∑ —ç–ª–µ–º–µ–Ω—Ç–∞
    const currentDescriptionElement = card.getElementsByClassName('card__description')[0]; // –ü–æ–ª—É—á–∞–µ–º —ç–ª–µ–º–µ–Ω—Ç –æ–ø–∏—Å–∞–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏
    const currentDescription = currentDescriptionElement.innerHTML; // –ò–∑–≤–ª–µ–∫–∞–µ–º —Ç–µ–∫—Å—Ç –æ–ø–∏—Å–∞–Ω–∏—è –∏–∑ —ç–ª–µ–º–µ–Ω—Ç–∞
    const currentTimeElement = card.getElementsByClassName('card__time')[0]; // –ü–æ–ª—É—á–∞–µ–º —ç–ª–µ–º–µ–Ω—Ç –≤—Ä–µ–º–µ–Ω–∏ –∫–∞—Ä—Ç–æ—á–∫–∏
    const currentTime = currentTimeElement.innerHTML; // –ò–∑–≤–ª–µ–∫–∞–µ–º —Ç–µ–∫—Å—Ç –≤—Ä–µ–º–µ–Ω–∏ –∏–∑ —ç–ª–µ–º–µ–Ω—Ç–∞
    const currentUserElement = card.getElementsByClassName('card__user')[0]; // –ü–æ–ª—É—á–∞–µ–º —ç–ª–µ–º–µ–Ω—Ç –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –∫–∞—Ä—Ç–æ—á–∫–∏
    const currentUser = currentUserElement.innerHTML; // –ò–∑–≤–ª–µ–∫–∞–µ–º —Ç–µ–∫—Å—Ç –∏–º–µ–Ω–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –∏–∑ —ç–ª–µ–º–µ–Ω—Ç–∞
    const id = Number(card.dataset.id); // –ü–æ–ª—É—á–∞–µ–º ID –∫–∞—Ä—Ç–æ—á–∫–∏ –∏–∑ –∞—Ç—Ä–∏–±—É—Ç–∞ data-id –∏ –ø—Ä–µ–æ–±—Ä–∞–∑—É–µ–º –µ–≥–æ –≤ —á–∏—Å–ª–æ
  
    // –û–ø—Ä–µ–¥–µ–ª—è–µ–º –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç –Ω–æ–≤–æ–≥–æ —Å—Ç–∞—Ç—É—Å–∞
    let newContainer; // –û–±—ä—è–≤–ª—è–µ–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é –¥–ª—è —Ö—Ä–∞–Ω–µ–Ω–∏—è –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞, –∫—É–¥–∞ –±—É–¥–µ—Ç –ø–µ—Ä–µ–º–µ—â–µ–Ω–∞ –∫–∞—Ä—Ç–æ—á–∫–∞
    if (newStatus === 'todo') {
      newContainer = todoContainer; // –ï—Å–ª–∏ –Ω–æ–≤—ã–π —Å—Ç–∞—Ç—É—Å 'todo', –Ω–∞–∑–Ω–∞—á–∞–µ–º –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä todoContainer
    } else if (newStatus === 'progress') {
      newContainer = progressContainer; // –ï—Å–ª–∏ –Ω–æ–≤—ã–π —Å—Ç–∞—Ç—É—Å 'progress', –Ω–∞–∑–Ω–∞—á–∞–µ–º –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä progressContainer 
    } else if (newStatus === 'completed') {
      newContainer = completedContainer; // –ï—Å–ª–∏ –Ω–æ–≤—ã–π —Å—Ç–∞—Ç—É—Å 'completed', –Ω–∞–∑–Ω–∞—á–∞–µ–º –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä completedContainer
    } 
  
    // –ü—Ä–æ–≤–µ—Ä–∫–∞ –ª–∏–º–∏—Ç–∞ –¥–ª—è In Progress
    if (newStatus === 'progress' && progressContainer.children.length > 5) {
      createModal(
        'You have accumulated unfulfilled tasks. Are you sure you want to add another task?',
        function () {
          confirmModal.remove(); // –§—É–Ω–∫—Ü–∏—è –æ—Ç–º–µ–Ω—ã: —É–¥–∞–ª—è–µ–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
        },
        function () {
          confirmModal.remove(); // –§—É–Ω–∫—Ü–∏—è –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è: —É–¥–∞–ª—è–µ–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –∏ –ø–µ—Ä–µ–Ω–æ—Å–∏–º –∫–∞—Ä—Ç–æ—á–∫—É
          moveCardToContainer();
        },
        'Progress' // –ü–µ—Ä–µ–¥–∞—ë–º –∫–ª–∞—Å—Å –¥–ª—è —É—Å—Ç–∞–Ω–æ–≤–∫–∏ —Ü–≤–µ—Ç–æ–≤ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
      );
    } else {
      moveCardToContainer(); // –ï—Å–ª–∏ –ª–∏–º–∏—Ç –Ω–µ –ø—Ä–µ–≤—ã—à–µ–Ω –∏–ª–∏ –Ω–æ–≤—ã–π —Å—Ç–∞—Ç—É—Å –Ω–µ 'progress', —Å—Ä–∞–∑—É –ø–µ—Ä–µ–º–µ—â–∞–µ–º –∫–∞—Ä—Ç–æ—á–∫—É
    }
  
    function moveCardToContainer() { // –§—É–Ω–∫—Ü–∏—è –¥–ª—è –ø–µ—Ä–µ–º–µ—â–µ–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏ –≤ –Ω–æ–≤—ã–π –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä
      // –°–æ–∑–¥–∞–µ–º –Ω–æ–≤—É—é –∫–∞—Ä—Ç–æ—á–∫—É —Å –ø–æ–º–æ—â—å—é createCard()
      const newCardHtml = createCard(newStatus, { // –°–æ–∑–¥–∞–µ–º HTML-–∫–æ–¥ –Ω–æ–≤–æ–π –∫–∞—Ä—Ç–æ—á–∫–∏ —Å –Ω—É–∂–Ω—ã–º —Å—Ç–∞—Ç—É—Å–æ–º –∏ –¥–∞–Ω–Ω—ã–º–∏
        id: id,
        title: currentTitle,
        description: currentDescription,
        time: currentTime,
        userName: currentUser
      });
  
      // –í—Å—Ç–∞–≤–ª—è–µ–º –Ω–æ–≤—É—é –∫–∞—Ä—Ç–æ—á–∫—É –≤ –Ω—É–∂–Ω—ã–π –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä
      newContainer.innerHTML = newContainer.innerHTML + newCardHtml;
  
      // –£–¥–∞–ª—è–µ–º —Å—Ç–∞—Ä—É—é –∫–∞—Ä—Ç–æ—á–∫—É
      card.remove();
  
      // –û–±–Ω–æ–≤–ª—è–µ–º –¥–∞–Ω–Ω—ã–µ –≤ Local Storage
      const tasks = getDate(); // –ü–æ–ª—É—á–∞–µ–º –º–∞—Å—Å–∏–≤ –∑–∞–¥–∞—á –∏–∑ Local Storage
      const taskToUpdate = tasks.find(function (task) { // –ù–∞—Ö–æ–¥–∏–º –∑–∞–¥–∞—á—É, –∫–æ—Ç–æ—Ä—É—é –Ω—É–∂–Ω–æ –æ–±–Ω–æ–≤–∏—Ç—å
        return task.id === id; // –£—Å–ª–æ–≤–∏–µ –ø–æ–∏—Å–∫–∞: ID –∑–∞–¥–∞—á–∏ —Å–æ–≤–ø–∞–¥–∞–µ—Ç —Å ID –ø–µ—Ä–µ–º–µ—â–∞–µ–º–æ–π –∫–∞—Ä—Ç–æ—á–∫–∏
      });
      taskToUpdate.status = newStatus; // –û–±–Ω–æ–≤–ª—è–µ–º —Å—Ç–∞—Ç—É—Å –∑–∞–¥–∞—á–∏ –Ω–∞ –Ω–æ–≤—ã–π
      setDate(tasks); // –°–æ—Ö—Ä–∞–Ω—è–µ–º –æ–±–Ω–æ–≤–ª–µ–Ω–Ω—ã–π –º–∞—Å—Å–∏–≤ –∑–∞–¥–∞—á –≤ Local Storage
  
      // –û–±–Ω–æ–≤–ª—è–µ–º —Å—á–µ—Ç—á–∏–∫–∏ –∏ –≤–∏–¥–∏–º–æ—Å—Ç—å –∫–Ω–æ–ø–∫–∏ —É–¥–∞–ª–µ–Ω–∏—è
      updateCounts(); // –û–±–Ω–æ–≤–ª—è–µ–º —Å—á–µ—Ç—á–∏–∫–∏ –∑–∞–¥–∞—á –≤ –∫–∞–∂–¥–æ–º —Å—Ç–∞—Ç—É—Å–µ
      updateDeleteButtonVisibility(); // –û–±–Ω–æ–≤–ª—è–µ–º –≤–∏–¥–∏–º–æ—Å—Ç—å –∫–Ω–æ–ø–∫–∏ "–£–¥–∞–ª–∏—Ç—å –≤—Å–µ"
    }
  }